<template>
    <section class="main-content">
        <loader :is-visible="isLoading"></loader>
        <div class="padding-t_10" id="reportstabsdata">
            <div class="left-menu-tab">
                <div class="col-md-12 p-0 ">
                    <div class="theme-primary partition">
                        <span class="p-name text-white">{{ Headerstext }}</span>
                    </div>
                </div>

                <!-- <ul class="nav nav-pills flex-column left-tab mt-2 responsivecustomtab nav-tabs">
                    <li v-for="tab in tabs" :key="tab.id" @click="activeTab = tab.id; changeTab(tab.id)"
                        :class="{ 'nav-item': true, 'active': activeTab === tab.id }">
                        <span :class="{ 'nav-link left-menu m-1 p-1': true, 'active': activeTab === tab.id }">
                            <i :class="tab.icon"></i> {{ tab.title }}
                        </span>
                    </li>
                </ul> -->

                <ul class="nav nav-pills flex-column left-tab mt-2 responivecustomtab  nav-tabs">
                    <li v-for="tab in tabs" :key="tab.id" @click="activeTab = tab.id, changeTab(tab.id)"
                        :class="{ 'nav-item': true, 'active': activeTab === tab.id }">
                        <span :class="{ 'nav-link left-menu': true, 'active': activeTab === tab.id }">
                            <i :class="tab.icon" class="p-1"></i>  {{ tab.title }}</span>
                    </li>
                </ul>


            </div>


            <div class="right-content">

                <div id="ReturnRequestHeading" class="theme-primary partition  p-actions-expand"><span
                        class="p-name text-white"> {{ tabHeaderText }}</span>


                </div>


                <div class="col-md-12 p-0" v-for="tab in tabs" :key="tab.id" v-show="activeTab === tab.id">

                    <EditUser v-if="isSettingsVisible && tab.id == 1" :isEditUser="isEditUser" :UserId="userId">

                    </EditUser>

                    <AssociateLicense v-if="isAssociateVisible && tab.id == 2" :UserId="userId">

                    </AssociateLicense>


                </div>


            </div>

        </div>




    </section>
</template>
<script>
import EditUser from "../Users/AddUser.vue";
import AssociateLicense from "../Users/AssociateLicense.vue"
export default {
    components: {
        EditUser,
        AssociateLicense
    },

    data() {
        return {
            tabHeaderText: "",
            isSettingsVisible: false,
            isAssociateVisible: false,
            activeTab: 1,
            isEditUser: '',
            tabs: [
                {
                    title: "General Settings",
                    icon: "fa fa-cogs",
                    id: 1
                },
                {
                    title: "Associate Licenses",
                    icon: "fa fa-plus",
                    id: 2
                },
                {
                    title: "Addresses",
                    icon: "fa fa-address-book",
                    id: 3
                },
                {
                    title: "Communication",
                    icon: "fa fa-phone",
                    id: 4
                },
                {
                    title: "Configuration",
                    icon: "fa fa-cog",
                    id: 5
                },
                {
                    title: "Contact Numbers",
                    icon: "fa fa-file",
                    id: 6
                },
                {
                    title: "Feedback",
                    icon: "fa fa-quote-left",
                    id: 7
                },
                {
                    title: "Goal",
                    icon: "fa fa-trophy",
                    id: 8
                },
                {
                    title: "Notes",
                    icon: "fa fa-sticky-note",
                    id: 9
                },
                {
                    title: "Notifications",
                    icon: "fa fa-bell",
                    id: 10
                },
                {
                    title: "Performance Review",
                    icon: "fa fa-file-text",
                    id: 11
                },
                {
                    title: "Skill",
                    icon: "fa fa-star",
                    id: 12
                },
                {
                    title: "Team Structure",
                    icon: "fa fa-sitemap",
                    id: 13
                },
                {
                    title: "User Break",
                    icon: "fa fa-clock-o",
                    id: 14
                }
            ],
            userId: null
        }
    },

    created() {
        debugger;

        var vm = this;
        vm.userId = parseInt(this.$route.params.id);
        vm.isSettingsVisible = true;
        vm.tabHeaderText = 'General Settings';
        vm.isEditUser = "EditUser"

    },

    methods: {

        changeTab(tab) {
       

            var vm = this;
            switch (tab) {
                case 1:
                    vm.isSettingsVisible = true;
                    vm.tabHeaderText = 'General Settings';

                    break;

                case 2:

                    vm.tabHeaderText = 'Associate Licenses';
                    vm.isAssociateVisible = true

                    break;

                case 3:
                    vm.tabHeaderText = 'Addresses';
                    break;

                case 4:
                    vm.tabHeaderText = 'Communication';
                    break;
                case 5:
                    vm.tabHeaderText = 'Configuration';
                    break;
                case 6:
                    vm.tabHeaderText = 'Contact Numbers';
                    break;
                case 7:
                    vm.tabHeaderText = 'Feedback';
                    break;
                case 8:
                    vm.tabHeaderText = 'Goal';
                    break;
                case 9:
                    vm.tabHeaderText = 'Notes';
                    break;
                case 10:
                    vm.tabHeaderText = 'Notifications';
                    break;
                case 11:
                    vm.tabHeaderText = 'Performance Review';
                    break;
                case 12:
                    vm.tabHeaderText = 'Skill';
                    break;
                case 13:
                    vm.tabHeaderText = 'Team Structure';
                    break;
                case 14:
                    vm.tabHeaderText = 'User Break';
                    break;


                default:
                    break;
            }
        },

        previousTab() {
            if (this.activeTab > 1) {
                this.activeTab--;
            }
        },
        nextTab() {
            if (this.activeTab <= this.tabs.length - 1) {
                this.activeTab++;
            }
        },
    },

}
</script>
<style></style>